/* CartPage.css */
/* Container for the entire cart page */
div.cart-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  /* Styling for the cart items section */
  ul.cart-items-list {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
  }
  
  li.cart-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #ccc;
  }
  
  .cart-item-container {
    display: flex;
    align-items: center;
  }
  
  .cart-item-image {
    width: 80px;
    height: 80px;
    object-fit: contain; /* Use "contain" instead of "cover" to fit the entire image within the container */
    margin-right: 10px;
  }
  
  .cart-item-info {
    display: flex;
    flex-direction: column;
  }
  
  .cart-item-name {
    font-size: 16px;
    font-weight: bold;
  }
  
  .cart-item-price {
    font-size: 14px;
  }
  
  /* Styling for the total price section */
  div.total-price-section {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
  
  .total-price {
    font-size: 18px;
    font-weight: bold;
    background-color: #f0f0f0;
    padding: 10px;
    border: 1px solid #ccc;
  }
  