.product-list-container {
  /* Add other styles for the container */
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.button-container {
  padding: 20px;
}

.sort-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.sort-button:hover {
  background-color: #0056b3;
}
