.flash-message {
    position: fixed;
    top: 100px;
    right: 10px;
    background-color: #075835;
    color: white;
    padding: 10px 20px;
    border-radius: 4px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    z-index: 9999;
  }
  
  .flash-message p {
    margin: 0;
    flex-grow: 1;
  }
  
  .flash-message button {
    border: none;
    background: transparent;
    color: white;
    font-weight: bold;
    cursor: pointer;
    padding: 0;
    margin-left: 10px;
  }
  